import React, { useState } from 'react';
import SearchForm from './components/SearchForm';
import UploadForm from './components/UploadForm';
import './App.css';

function App() {
  const [searchResults, setSearchResults] = useState([]);

  const handleSearch = async (keyword, parameter) => {
    console.log('Searching with keyword:', keyword);
    console.log('Searching with parameter:', parameter);

    try {
      const response = await fetch(`http://localhost:3333/api/search?keyword=${keyword}&parameter=${parameter}`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      setSearchResults(data.filteredData || []);
    } catch (error) {
      console.error('Error fetching search results:', error);
      alert('Không thể tìm kiếm. Vui lòng thử lại sau.');
    }
  };

  const handleUpload = async (file) => {
    const formData = new FormData();
    formData.append('file', file);

    try {
      const response = await fetch('http://localhost:3333/api/upload', {
        method: 'POST',
        body: formData,
      });
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      alert(data.message);
    } catch (error) {
      console.error('Error uploading file:', error);
      alert('Không thể tải tệp lên. Vui lòng thử lại sau.');
    }
  };

  const handleUpdateQuantity = async (maSanPham, quantity) => {
    try {
      const response = await fetch('http://localhost:3333/api/update-quantity', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ maSanPham, quantity }),
      });
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      if (data.error) {
        alert(data.error);
      } else {
        alert('Cập nhật số lượng thành công');
        handleSearch(); // Có thể cần phải truyền tham số tìm kiếm hiện tại nếu cần
      }
    } catch (error) {
      console.error('Error updating quantity:', error);
      alert('Không thể cập nhật số lượng. Vui lòng thử lại sau.');
    }
  };

  return (
    <div className="App">
      <h1>Quản lý Kho</h1>
      <SearchForm onSearch={handleSearch} />
      <UploadForm onUpload={handleUpload} />
      <div className="results">
        <h2>Kết quả Tìm kiếm</h2>
        <ul>
          {searchResults.map(result => (
            <li key={result['Mã sản phẩm ']}>
              <p><strong>Tên sản phẩm:</strong> {result['Tên sản phẩm']}</p>
              <p><strong>Mã sản phẩm:</strong> {result['Mã sản phẩm ']}</p>
              <p><strong>Thông số sản phẩm:</strong> {result['Thông số sản phẩm']}</p>
              <p><strong>Vị trí:</strong> {result['Vị trí']}</p>
              <p><strong>Số lượng sản phẩm:</strong> {result['Số lượng sản phẩm']}</p>
              <button onClick={() => handleUpdateQuantity(result['Mã sản phẩm '], -1)}>Giảm 1</button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default App;